@font-face {
    font-family: 'noto_serifregular';
    src: url('../assets/fonts/notoserif-regular-webfont.woff2') format('woff2'),
    url('../assets/fonts/notoserif-regular-webfont.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

body, html {
    height: 100%;
    min-height: 100%;
    padding: 0;
    margin: 0;
}

#root {
    height: 100%;
    min-height: 100%;
    min-width: 100%;
}

.wrapper {
    height: 100%;
}

.centered {
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%,-50%);
        -ms-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
}

.centered div {
    font-family: noto_serifregular;
    text-align: center;
    color: #fff;
}

